import React from 'react'
import { Button, Text } from '@instacart/cocktail'
import interpolate from '@instacart/enterprise-services/lang/interpolate'
import { useConfig, useLang } from '@app/services/config/hooks'
import { createStyles } from '@app/react/common/hooks/createStyles'
import { ExpressPlan } from '@app/services/express/types'

interface ExpressPromoPlanChangeProps {
  promoPlan: ExpressPlan
  onButtonClick: () => void
}

const useStyles = createStyles(theme => ({
  mainComponent: {
    marginTop: '20px',
  },
  promoContent: {
    borderTop: `2px solid ${theme.colors.systemGrayscale20}`,
    paddingTop: '20px',
  },
  redeemPromoButton: {
    fontWeight: 'bold',
    marginTop: '10px',
    paddingLeft: '0px',
  },
}))

const ExpressPromoPlanChange = (props: ExpressPromoPlanChangeProps) => {
  const styles = useStyles()
  const config = useConfig()
  const lang = useLang()

  const { onButtonClick, promoPlan } = props

  const promoPrice = parseFloat(promoPlan.price)
  const promoFullPrice = parseFloat(promoPlan.full_price || promoPlan.price)
  const formattedPrice = `${config.currencySymbol}${Math.round(promoPrice)}`
  const formattedFullPrice = `${config.currencySymbol}${Math.round(promoFullPrice)}`
  const discountPercentage = Math.abs(Math.round((promoPrice / promoFullPrice - 1.0) * 100))

  return (
    <div css={styles.mainComponent}>
      <div css={styles.promoContent}>
        <Text>
          {interpolate(lang.icExpress.profilePage.promoPlanChange.promoText, {
            discountPercentage,
            fullPrice: formattedFullPrice,
            price: formattedPrice,
            term: promoPlan.term_length || '',
          })}
        </Text>
        <Button
          data-test="promo-plan-click"
          variant="link"
          css={styles.redeemPromoButton}
          onClick={onButtonClick}
        >
          {interpolate(lang.icExpress.profilePage.promoPlanChange.promoLinkText, {
            discountPercentage,
          })}
        </Button>
      </div>
    </div>
  )
}

export default ExpressPromoPlanChange
